        0px 0px 60px #9D00FF;
}

.neon-heading.second {
    text-shadow: 0px 0px 10px #33FF00,
        0px 0px 20px #33FF00,
        0px 0px 30px #33FF00,
        0px 0px 40px #33FF00,
        0px 0px 50px #33FF00,
        0px 0px 60px #33FF00;
    -webkit-animation: pulsating 5s infinite;
            animation: pulsating 5s infinite;
}

/* FLICKERING */
span.flickering-1 {
    -webkit-animation: flickering-1 6s infinite;
            animation: flickering-1 6s infinite;
}

span.flickering-2 {
    -webkit-animation: flickering-2 3s infinite;
            animation: flickering-2 3s infinite;
}

span.flickering-3 {
    -webkit-animation: flickering-3 5s infinite;
            animation: flickering-3 5s infinite;
}

span.flickering-4 {
    -webkit-animation: flickering-4 8s infinite;
            animation: flickering-4 8s infinite;
}

/* ANIMATIONS */
@-webkit-keyframes pulsating {
    0% {
        text-shadow: 0px 0px 0px;
        opacity: 0;
    }

    50% {
        text-shadow: 0px 0px 10px #33FF00,
            0px 0px 20px #33FF00,
            0px 0px 30px #33FF00,
            0px 0px 40px #33FF00,
            0px 0px 50px #33FF00,
            0px 0px 60px #33FF00;
        opacity: 1;
    }

    100% {
        text-shadow: 0px 0px 0px;
        opacity: 0;
    }
}
@keyframes pulsating {
    0% {
        text-shadow: 0px 0px 0px;
        opacity: 0;
    }

    50% {
        text-shadow: 0px 0px 10px #33FF00,
            0px 0px 20px #33FF00,
            0px 0px 30px #33FF00,
            0px 0px 40px #33FF00,
            0px 0px 50px #33FF00,
            0px 0px 60px #33FF00;
        opacity: 1;
    }

    100% {
        text-shadow: 0px 0px 0px;
        opacity: 0;
    }
}

